 message: '请填写必要信息',\n                            type: 'warning'\n                        });\n                        return false;\n                    }\n                });\n            },\n            ...mapActions(['signin'])\n        },\n        components: {\n            'sg-body': Body\n        }\n    }\n</script>\n"],"sourceRoot":"webpack://"}]);

