fter {\n        -webkit-transform: scaleX(1);\n        transform: scaleX(1);\n    }\n\n    .topic-line-meta {\n        margin-top: .2em;\n    }\n\n    .topic-line-body {\n\n    }\n</style>\n<script>\n    import md5 from 'md5'\n\n    export default{\n        name: 'sg-topic-Line',\n        data() {\n            return {avatarUrl: preLoadData.userImg}\n        },\n        props: {\n            data: {\n                type: Object\n            }\n        },\n        methods: {\n            getAreaUrl(id){\n                return '/a/' + id;\n            },\n            getTopicUrl(id){\n                return '/t/' + id;\n            },\n            getUserUrl(id){\n                return '/i/' + id;\n            },\n            getAvatar(mail){\n                return this.avatarUrl.replace('{md5}', md5(mail));\n            },\n            splitText(str){\n                if (!str) return [];\n                return str.split(',');\n            }\n        },\n        components: {}\n    }\n</script>\n"],"sourceRoot":"webpack://"}]);

